.Nav {

    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;

    display: flex;
    justify-content: space-between;

    height: 30px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;

}

.nav-black {
    background-color: black;
}

.nav-logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain;
}

.nav-avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;

}